$background-animation-time: 3s;

.first-slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.first-slide__background {
  position: absolute;
  top: calc(50vh - 25vw);
  left: calc(25vw);
  z-index: -1;
  overflow-y: hidden;

  width: 50vw;
  height: 50vw;
  background: $orange;
  border-radius: 50%;
  transform: scale(5);
  animation: background-appearance $background-animation-time ease-in-out;
}
.first-slide__background--blue {
  background: $blue;
}
.first-slide__background--rotating {
  top: 0;
  left: 7%;

  border-radius: 0;
  width: 86%;
  height: 100%;

  transition: transform 1s;
  transform-origin: center;
  transform: scaleY(1.5) rotate(15deg);
}
@keyframes background-appearance {
  from {
    transform: scale(0);
  }
  10% {
    transform: scale(0);
  }
  40% {
    transform: scale(3);
  }
  50% {
    transform: scale(3);
  }
  to {
    transform: scale(7);
  }
}

.first-slide__title {
  @include text-styles(36px, 600);
  width: 70%;
}
