@import 'fonts.scss';
@import 'variables.scss';
@import 'mixins.scss';

$window-height: 100vh;

html {
  overflow: hidden;
}
body {
  margin: 0;
  overflow-y: hidden;
  box-sizing: border-box;
}

.main-container {
  width: 100%;
  height: $window-height;
  overflow-y: hidden;
}

.slide {
  height: 100vh;
  overflow: hidden;
}
.slide__container {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.slide--hide {
  transform: translateX(200%);
  opacity: 0;
}
.hide-animation {
  transition: transform .6s, opacity .6s;
  transform: translateX(-200%);
  opacity: 0;
}
.show-animation {
  transition: transform .6s, opacity .6s;
  transform: translateX(0);
  opacity: 1;
}

.background--hide {
  transform: translateX(700%);
  opacity: 0;
}
.background-hide-animation {
  transition: transform .6s, opacity .6s;
  transform: translateX(-700%);
  opacity: 0;
}
.background-show-animation {
  transition: transform .6s, opacity .6s;
  transform: translateX(0);
  opacity: 1;
}

@import 'diagonal-animations.scss';

@import 'slides/first-slide.scss';
@import 'slides/second-slide.scss';
@import 'slides/third-slide.scss';
@import 'slides/fourth-slide.scss';
@import 'slides/fifth-slide.scss';
