@mixin text-animation($time) {
  animation: text-animation $time ease-in-out;
}
@keyframes text-animation {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  60% {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@mixin text-styles(
  $size,
  $weight,
) {
  color: #fff;
  font-size: $size;
  font-weight: $weight;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  word-spacing: 3px;
}
