$top-line-animation-time: 1.5s;
$bottom-line-animation-time: .5s;
$show-bottom-text-time: 2.5s;
$slider-disappearance-time: 0.7s;
$slider-disappearance-delay-time: $show-bottom-text-time + .2s;

.second-slide__image {
  //position: absolute;
  //top: 0;
  //left: 0;

  width: 70%;
  object-fit: contain;
}

.second-slide {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  animation: slide-disappearance $slider-disappearance-time ease-in-out $slider-disappearance-delay-time;
}
@keyframes slide-disappearance {
  to {
    opacity: 0;
  }
}

.second-slide__line-top {
  width: 100%;
  height: 75vh;
  position: absolute;
  top: 0;
  z-index: 1;
}
.second-slide__blind-top {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 2;
  width: 0;
  height: 75vh;
  background-color: #fff;
  transform: rotateX(180deg) scaleX(-1);
  animation: second-slide-open-blind-top $top-line-animation-time ease-out;
}
@keyframes second-slide-open-blind-top {
  from {
    width: 100%;
  }
  52% {
    width: 100%;
  }
  to {
    width: 0;
  }
}

.second-slide__line-bottom {
  width: 100%;
  height: 75vh;
  position: absolute;
  bottom: 0;
  z-index: 3;
}
.second-slide__blind-bottom {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 4;
  width: 0;
  height: 75vh;
  background-color: #fff;
  animation: open-blind-bottom $bottom-line-animation-time ease-out;
}
@keyframes open-blind-bottom {
  from {
    width: 100%;
  }
  to {
    width: 0;
  }
}

.second-slide__text-bottom {
  position: absolute;
  z-index: 5;
  bottom: 10vh;
  left: 0;
  width: 100%;
  box-sizing: border-box;

  @include text-styles(36px, 600);
  animation: text-bottom-appearance $show-bottom-text-time ease-in-out;
}
@keyframes text-bottom-appearance {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  60% {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
