.fifth-slide__background {
  background: $red;
  transition: transform 1s;
  transform: scale(7);
}
.fifth-slide__background--constriction {
  transform: scale(3);
}
.fifth-slide__background--dilation {
  transform: scale(7);
}

.fifth-slide__text {
  width: 60%;
}
.fifth-slide__text--animation {
  @include text-animation(1.7s);
}
