$third-slide-top-line-animation-time: .3s;
$third-slide-middle-line-animation-time: .7s;
$third-slide-bottom-line-animation-time: 1.2s;

.third-slide__text {
  //margin: auto;
}
.third-slide__image {
  width: 100%;
  object-fit: contain;
}
.third-slide__image--top {
  margin-right: -40px;
}
.third-slide__image--bottom {
  margin-left: -30px;
  height: 36vh;
  aspect-ratio: 191/108;
}

.third-slide__container {
  justify-content: space-between;
}
